body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  padding: 25px 5px;
  background-color: aliceblue;
  width: 100%;
  color: #052a8f;
  text-align: center;
  border-bottom: 10px solid #052a8f;
}

h1 {
  font-family: "Courier New", Courier, monospace;
  font-size: 55px;
  font-weight: bold;
}

p {
  font-weight: bold;
}

.navbar {
  padding: 20px 20px;
  margin: 0 auto;
}

#navbar {
  background-color: cornflowerblue;
  color: aliceblue;
}

.navbar-collapse {
  margin: 0 auto;
  justify-content: center;
  align-self: flex-start;
}

.searchbar button {
  border: 1px solid #052a8f;
}

.searchbar button:hover {
  background-color: #052a8f;
  color: aliceblue;
}

.form-control {
  text-align: center;
}

.datatable .col .pointer:after {
  font-size: 80%;
  content: "";
  margin-right: 18px;
}

.datatable .col .pointer:hover:after {
  font-size: 80%;
  content: "ðŸ”»";
  margin-right: 0;
}

th {
  background-color: aliceblue;
  color: #052a8f;
}
